import styled from "styled-components";
import { openUrlInNewTab } from "../utils/utils";
import useWindowDimensions from "./useWindowDimensions";

const ProjectItem = ({ title, description, url }) => {
  const { width } = useWindowDimensions();

  return (
    <Project>
      <h3>{title}</h3>
      <div className='imgContainer'>
        <img src={testImgSrc} alt='project' />
        <div className='fade' onClick={() => openUrlInNewTab(url)}>
          <div className='textContainer'>
            <p>{description}</p>
          </div>
        </div>
        {width < 768 && (
          <div className='description'>
            <p>{description}</p>
          </div>
        )}
      </div>
    </Project>
  );
};

const Project = styled.div`
  h3 {
    color: #313131;
    font-size: 2rem;
    padding: 1rem 0rem;
    text-align: center;
  }
  .imgContainer {
    position: relative;
    width: 300px;
    height: 200px;
    cursor: pointer;
    img {
      z-index: 1;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .fade {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #0000006e;
      z-index: 10;
      opacity: 0;
      transition: all 250ms ease-in-out;
      &:hover {
        opacity: 1;
      }
      p {
        color: white;
      }
    }
  }
  .textContainer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 2rem;
  }
  .description {
    p {
      color: black;
      line-height: 1.8rem;
      letter-spacing: 1px;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    }
  }
`;

export default ProjectItem;

const testImgSrc =
  "data:image/jpeg;base64,";
